//-----------------------------------------------------------------------//
// This is the main file for animate.scss. All partials and helpers will
// be pulled into this file by the code below. If you want to decrease
// the size of this file you can comment out sections below, but remember
// that due to the nature of mixins it won't hurt your actual performance
// to leave it all in here!
//
// So, without further ado, let's get started!
//-----------------------------------------------------------------------//
//-----------------------------------------------------------------------//
// VARIABLES
//
// First, we'll set up some sensible defaults for the helper functions.
// These are prefixed global- to separate them from the variables used
// in each mixin.
//-----------------------------------------------------------------------//
$global-duration: 1s !default;
$global-function: ease !default;
$global-delay: .3s !default;
$global-count: 1 !default;
$global-fill: both !default;
$global-visibility : hidden !default;
$prefixes: (webkit moz ms o spec);
$prefix-for-webkit: true !default;
$prefix-for-mozilla: true !default;
$prefix-for-microsoft: true !default;
$prefix-for-opera: true !default;
$prefix-for-spec: true !default; // required for keyframe mixin
//-----------------------------------------------------------------------//
// HELPERS
// These are functions that are called from the mixins to setup stuff
// like duration, fill-mode etc.
//-----------------------------------------------------------------------//
@import "partials/helpers";
// There's a small bug in webkit that requires a CSS3 property to be set on an element.
// Read more about it at http://bit.ly/NEdoDq
body {
    -webkit-backface-visibility: hidden;
}

